<template>
	<view class="content">
		<scroll-view class="_content" :scroll-y="true" @scrolltolower="get_more">
			<view class="_lists" v-for="(n, index) in recogmentList" :key="index">
				<image class="header_img" :src="n.photo" mode="aspectFill" @click="moving_details(n.id)"></image>
				<view class="_right">
					<view class="user_name">
						<text>{{n.name}}</text>
						<!-- <text class="guanzhu">+ 关注</text> -->
					</view>
					<text class="text_content">{{n.content}}</text>
					<!-- 图片 -->
					<image v-if="n.imageslist.length < 2 && n.imageslist.length !== 0" class="big_images" :src="n.imageslist[0]" mode="aspectFill"></image>
					<view class="img_content" v-if="n.imageslist.length > 1">
						<image class="images" v-for="(m, index1) in n.imageslist" :key="index1" :src="m" mode="aspectFill"></image>
					</view>
					<text class="time_content">{{n.timeout}}</text>
					<!-- 评论 -->
					<view class="comment_content">
						<!-- <view class="comment_list" v-for="(i, index2) in 3" :key="index2">
							<text class="comment_name">有理想的咸鱼:</text>
							<text>无聊</text>
						</view> -->
					</view>
					<view class="bottom_tab">
						<text>{{n.dianzan}}喜欢</text>
						<text>{{n.pinglun}}评论</text>
						<view class="content_event">
							<i class="iconfont" :class="{'like' : n.pras }" @click="like_moving(index)">&#xe755;</i>
							<i class="iconfont" @click="moving_details(n.id)">&#xe638;</i>
							<i class="iconfont">&#xe65f;</i>
						</view>
					</view>
				</view>
			</view>
			<uni-load-more :status="loadingType"></uni-load-more>
		</scroll-view>
		<view class="_add" @click="go_add">
			<i class="iconfont">&#xe615;</i>
		</view>
		
	</view>
</template>

<script>
	import uniLoadMore from '../components/uni-load-more/uni-load-more.vue'
	import test_img from '../../static/img4.jpg'
	import _ from 'lodash'
	export default {
		data() {
			return {
				recogmentList: [],
				test_img: test_img,
				loadingType: 'more',
				pageNum: 1,
				more_page: 0
			}
		},
		components:{
			uniLoadMore
		},
		onShow() {
			
		},
		onLoad(option){
			uni.showLoading({
			    title: '加载中'
			});
			this.init()
		},
		methods: {
			init (n) {
				this.$http
				.post('/api/instantdt', 
				{
					userid: uni.getStorageSync('userId'),
					"pageNum": this.pageNum,
					"pageSize": 5
				}, {})
				.then(res => {
					_.each(res.data.rows, o => {
						this.recogmentList.push(o)
					})
					uni.hideLoading()
					this.more_page = Math.ceil(res.data.total/5)
					if (this.pageNum === this.more_page) {
						this.loadingType = 'nomore'
					}
					this.pageNum++
				})
				.catch(err => {
				})
			},
			get_more () {
				if (this.pageNum <= this.more_page) {
					uni.showLoading({
					    title: '加载中'
					});
					this.init()
				}
			},
			go_add () {
				uni.navigateTo({
					url: '/pages/found/writeabout'
				})
			},
			like_moving (n) {
				this.$http
				.post('/api/praise', 
				{
					praiseuserid: uni.getStorageSync('userId'),
					dynamicid: this.recogmentList[n].id
				}, {})
				.then(res => {
					this.recogmentList[n].pras = !this.recogmentList[n].pras
					if (this.recogmentList[n].pras) {
						this.recogmentList[n].dianzan++
					} else {
						this.recogmentList[n].dianzan--
					}
				})
				.catch(err => {
				})
			},
			moving_details (n) {
				uni.navigateTo({
					url: `/pages/index/userinfo?id=${n}`
				})
			}
		}  
	}
</script>

